.tile {
  display: -ms-flexbox;
  display: flex;
  width: 35vw;
  height: 35vw;
  border: solid 1px black; 
  margin: 10px;
  font-size: .8rem;
  padding: 10px;
  -webkit-transition: all 3s ease;
  -o-transition: all 3s ease;
  transition: all 3s ease;
}

h1 {
  font-size: 1.4rem;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: 0;
}

h2 {
  font-size: 1rem;
}

a {
  text-decoration: none;
  color: black;
}


